import item_qrcode from '@/bra/common/item_qrcode.vue';
export default {
    components: {
        item_qrcode
    },
    data() {
        return {
            page_name: 'yxm_user_my_poster',
            module: 'yxm',
            is_page_loaded: false,
            page_data: {},
            ifShow: true,
            val: '', // 要生成的二维码值
            size: 200, // 二维码大小
            unit: 'upx', // 单位
            background: '#b4e9e2', // 背景色
            foreground: '#309286', // 前景色
            pdground: '#32dbc6', // 角标色
            icon: '', // 二维码图标
            iconsize: 40, // 二维码图标大小
            lv: 3, // 二维码容错级别 ， 一般不用设置，默认就行
            onval: false, // val值变化时自动重新生成二维码
        }
    },
    onShow() {
        this.show_toast('点击图片保存到手机相册');
        if (!this.is_page_loaded) {
            this.onMhcmsPull();
        }
    },

    methods: {

        onMhcmsPull() {
            this.bra_common_page(this.module, this.page_name, {}, true, (data) => {

                this.page_data = this.elements.page_data;

                this.val = this.bra_web('bra_app', 'index', 'share_install', {
                    refer: this.page_data.user.id,
                    appid: this.site_info.bra_app_id
                });

                this.is_page_loaded = true;

                uni.stopPullDownRefresh();
            });
        },

        capture() {
            var pages = getCurrentPages();
            var page = pages[pages.length - 1];
            console.log(pages);
            var bitmap = null;
            var currentWebview = page.$getAppWebview();
            bitmap = new plus.nativeObj.Bitmap('amway_img');
            // 将webview内容绘制到Bitmap对象中
            currentWebview.draw(bitmap, function() {
                console.log('截屏绘制图片成功');
                bitmap.save("_doc/a.jpg", {}, function(i) {
                    console.log('保存图片成功：' + JSON.stringify(i));
                    uni.saveImageToPhotosAlbum({
                        filePath: i.target,
                        success: function() {
                            bitmap.clear(); //销毁Bitmap图片
                            uni.showToast({
                                title: '保存图片成功',
                                mask: false,
                                duration: 1500
                            });
                        }
                    });
                }, function(e) {
                    console.log('保存图片失败：' + JSON.stringify(e));
                });
            }, function(e) {
                console.log('截屏绘制图片失败：' + JSON.stringify(e));
            });
        }
    }
}
